.team-card{
    display:flex;
    flex-direction: row;
    margin: 30px 120px;
    border: none;
    width: 80em;
    margin-top: 80px;
}

.team-card img{
    width: 300px;
    height: 300px;
    margin: 30px 40px;
}

.team-body h1{
    font-weight: 800;
    margin-bottom: 20px;
}

.team-body p{
    margin-bottom: 20px;
}

.team-btn{
    background-color: #b90edf;
    border:none;
    border-radius:50px;
    margin-top: 10px;
}

.team-btn h1{
    font-family: 'Raleway', sans-serif;
    font-weight: 800;
    font-size: 24px;
    margin: 8px 16px;
    color:white
}

.team-btn p{
    line-height: 2px;
    color:#e6a8f4;
    margin: 10px 20px;
    margin-bottom:18px;
}

.team-btn{
    transition: 0.3s;
}

.team-btn:hover{
    background-color:#b90edf;
    transform: scale(1.05);
    transition: 0.3s;
    box-shadow: 0 8px 8px  #b76bbc;
}

@media(max-width:480px){

    .team-card img{
        width: 270px;
        height: 270px;
        margin: 10px 40px;
    }

    .team-card{
        display: flex;
        flex-direction: column;
        margin: 20px 30px;
        width: 400px;
        align-items: center;
    }

    .team-body p{
        margin-bottom: 25px;
        font-size: 18px;
    }

    .team-btn{
        background-color: #b90edf;
        border:none;
        border-radius:50px;
    }
    
    .team-btn h1{
        font-family: 'Raleway', sans-serif;
        font-weight: 800;
        margin: 8px 30px;
        color:white;
        font-size: 20px;
    }
    
    .team-btn p{
        line-height: 2px;
        color:#e6a8f4;
        margin: 10px 30px;
        margin-bottom:18px;
        font-size: 14px;
    }
    
    .team-btn{
        transition: 0.3s;
    }

}

@media(max-width:420px){

    .team-card{
        display: flex;
        flex-direction: column;
        margin: 20px 30px;
        width: 360px;
        align-items: center;
    }
}

@media(max-width: 360px){
    .team-card{
        display: flex;
        flex-direction: column;
        margin: 20px 30px;
        width: 310px;
        align-items: center;
    }

    .team-card img{
        width: 270px;
        height: 270px;
    }


    .team-btn p{
        margin-bottom: 20px;
        font-size: 15px;
    }
}

@media(max-width: 320px){
    .team-card{
        display: flex;
        flex-direction: column;
        margin: 20px 30px;
        width: 280px;
        align-items: center;
    }

    .team-card img{
        width: 250px;
        height: 250px;
    }

    .team-body h1{
        font-size: 18px;
    }

    .team-body p{
        font-size: 16px;
    }

    .team-btn h1{
        font-family: 'Raleway', sans-serif;
        font-weight: 800;
        margin: 8px 30px;
        color:white;
        font-size: 16px;
    }
    
    .team-btn p{
        line-height: 2px;
        color:#e6a8f4;
        margin: 10px 30px;
        margin-bottom:18px;
        font-size: 12px;
    }
}