.form-tag{
    display:inline-flex;
    flex-direction: row;
    margin: 80px 120px;
    margin-top:120px;
}

.contact-credentials h1{
    font-weight: 800;
    font-family: 'Raleway';
}

.contact-credentials h2{
    font-weight: 800;
    font-size: 27px;
}

.contact-credentials{
    margin-right: 50px;
}

.contact-credentials p {
    margin-top: 12px;;
}

.form-label{
    font-weight: 700;
}

.form-form{

    margin-left: 40px;
    margin-top:40px
}

span{
    font-weight: 700;
}

.mb-3{
    width: 35rem;
    display:flex;
    flex-direction: column;
}

.contact-btn{
    background-color: #b90edf;
    border:none;
    border-radius:50px;
    display:block;
    padding: 0 10px;
}

.contact-btn h1{
    font-family: 'Raleway', sans-serif;
    font-weight: 800;
    font-size: 24px;
    margin: 8px 26px;
    color:white
}

.contact-btn p{
    line-height: 2px;
    color:#e6a8f4;
    margin: 10px 30px;
    margin-bottom:18px;
}

.contact-btn{
    transition: 0.3s;
}

.contact-btn:hover{
    background-color:#b90edf;
    transform: scale(1.05);
    transition: 0.3s;
    box-shadow: 0 8px 8px  #b76bbc;
}

.contact-btn:active{
    background-color: #b90edf;
}


@media(max-width:480px){

    .form-tag{
        display:flex;
        flex-direction: column;
        margin: 20px 30px;
        width: 400px
    }

    .contact-credentials{
        margin: 10px 30px;
    }

    .contact-credentials h1{
        font-weight: 800;
        font-size: 22px;
    }

    .contact-credentials p{
        font-size: 18px;
    }

    .contact-credentials h2{
        font-weight: 800;
        font-size: 20px;
    }

    .form-form{
        width: 400px;
        margin: 10px 30px;
    }

    .mb-3{
        width: 380px;
        display:flex;
        flex-direction: column;
    }

    .contact-btn{
        background-color: #b90edf;
        border:none;
        border-radius:50px;
    }
    
    .contact-btn h1{
        font-family: 'Raleway', sans-serif;
        font-weight: 800;
        margin: 8px 30px;
        color:white;
        font-size: 20px;
    }
    
    .contact-btn p{
        line-height: 2px;
        color:#e6a8f4;
        margin: 10px 30px;
        margin-bottom:18px;
        font-size: 14px;
    }
    
    .contact-btn{
        transition: 0.3s;
    }
}

@media(max-width: 420px){
    .form-tag{
        display:flex;
        flex-direction: column;
        margin: 20px 30px;
        width: 360px
    }

    .mb-3{
        width: 300px;
        display:flex;
        flex-direction: column;
    }
}

@media(max-width:360px){
    .form-tag{
        display:flex;
        flex-direction: column;
        margin: 20px 30px;
        width: 310px
    }

    .mb-3{
        width: 260px;
        display:flex;
        flex-direction: column;
    }
}

@media(max-width: 320px){
    .form-tag{
        display: flex;
        flex-direction: column;
        margin: 20px 30px;
        width: 280px;
        align-items: center;
    }

    .mb-3{
        width: 220px;
        display:flex;
        flex-direction: column;
    }

    .form-form{
        width: 280px;
        margin: 10px 30px;
    }

    .contact-btn h1{
        font-family: 'Raleway', sans-serif;
        font-weight: 800;
        margin: 8px 30px;
        color:white;
        font-size: 16px;
    }
    
    .contact-btn p{
        line-height: 2px;
        color:#e6a8f4;
        margin: 10px 30px;
        margin-bottom:18px;
        font-size: 12px;
    }
}