.card-body h1{
    font-weight: 800;
    margin-bottom: 20px;
}

.about-card{
    display:flex;
    flex-direction: row;
    margin: 30px 120px;
    border: none;
    width: 80em;
}

.about{
    margin-top: 100px;
}

.about-text{
    margin-top: 15px;
}

.card-body p{
    margin-bottom: 20px;
}

.about-h3{
    font-size: 20px;
    font-family: 'Raleway';
    font-weight: 800;
    margin-bottom: 20px;
}

    
.about-btn{
    background-color: #b90edf;
    border:none;
    border-radius:50px;
}
    
.about-btn h1{
    font-family: 'Raleway', sans-serif;
    font-weight: 800;
    margin: 8px 30px;
    font-size:32px;
    color:white
}

.about-btn p{
    line-height: 2px;
    color:#e6a8f4;
    margin: 10px 30px;
    margin-bottom:18px;
}

.about-btn{
    transition: 0.3s;
}

.about-btn:hover{
    background-color:#b90edf;
    transform: scale(1.05);
    transition: 0.3s;
    box-shadow: 0 8px 8px  #b76bbc;
}


@media(max-width: 480px){

    .about{
        align-items: center;
    }

    .about-card img{
        margin-top: 10px;
        width: 380px;
        height: 271px;
    }

    .about-card{
        display:flex;
        flex-direction: column;
        margin: 20px 30px;
        border: none;
        width: 400px;
    }

    .about-body h1{
        font-weight: 800;
        margin-bottom: 20px;
        font-size: 30px;
    }

    .about-body p{
        font-size: 18px;
    }

    .about-body h2{
        font-size: 21px;
    }

    .about-h3{
        font-size: 18px;
        font-family: 'Raleway';
        font-weight: 800;
        margin-bottom: 20px;
    }

    .about-btn{
        background-color: #b90edf;
        border:none;
        border-radius:50px;
    }
        
    .about-btn h1{
        font-family: 'Raleway', sans-serif;
        font-weight: 800;
        margin: 8px 30px;
        color:white;
        font-size: 20px;
    }
    
    .about-btn p{
        line-height: 2px;
        color:#e6a8f4;
        margin: 10px 30px;
        margin-bottom:18px;
        font-size: 14px;
    }
    
    .about-btn{
        transition: 0.3s;
    }
    
    .about-btn:hover{
        background-color:#b90edf;
        transform: scale(1.05);
        transition: 0.3s;
        box-shadow: 0 8px 8px  #b76bbc;
    }
}

@media(max-width:420px){
    .about-card{
