
.hero-card{
    display:flex;
    flex-direction: row;
    margin: 30px 120px;
    border: none;
    width: 80em;
}

.hero-card img{
    width:612px;
    height:487px;
}

.hero-body{
    margin-top: 70px;
}

.hero-body h1{
    font-size: 30px;
    margin-bottom: 15px;
    font-family: 'Raleway';
    font-weight: 800;
}

.hero-body p{
    margin-bottom: 25px;
}

.hero-btn{
    background-color: #b90edf;
    border:none;
    border-radius:50px;
}

.hero-btn h1{
    font-family: 'Raleway', sans-serif;
    font-weight: 800;
    margin: 8px 30px;
    color:white
}

.hero-btn p{
    line-height: 2px;
    color:#e6a8f4;
    margin: 10px 30px;
    margin-bottom:18px;
}

.hero-btn{
    transition: 0.3s;
}

.hero-btn:hover{
    background-color:#b90edf;
    transform: scale(1.05);
    transition: 0.3s;
    box-shadow: 0 8px 8px  #b76bbc;
}

@media(max-width:768px){
    .hero-card img{
        margin-right: 50px;
    }
}

@media(max-width:480px){

    .hero-card{
        display: flex;
        flex-direction: column;
        margin: 20px 30px;
        width: 400px;
        align-items: center;
    }

    .hero-card img{
        width: 380px;
        height:292px;
        margin: 0 0;
    }

    .hero-body{
        margin-top:20px;
    }

    .hero-body h1{
        font-size: 22px;
        margin-bottom: 25px;
        font-family: 'Raleway';
        font-weight: 700;
    }

    .hero-body p{
        margin-bottom: 25px;
        font-size: 18px;
        
    }

    .hero-btn{
        background-color: #b90edf;
        border:none;
        border-radius:50px;
    }
    
    .hero-btn h1{
        font-family: 'Raleway', sans-serif;
        font-weight: 800;
        margin: 8px 30px;
        color:white;
        font-size: 20px;
    }
    
    .hero-btn p{
        line-height: 2px;
        color:#e6a8f4;
        margin: 10px 30px;
        margin-bottom:18px;
        font-size: 14px;
    }
    
    .hero-btn{
        transition: 0.3s;
    }
    
}

