.services{
    display:grid;
    grid-template-columns: 1fr 1fr;
}

.services-card{
    width: 40rem;
    margin: 30px;
    margin-left: 50px;
    margin-right: 30px;
    border:none;
}

.services-btn{
    background-color: #b90edf;
    border:none;
    border-radius:50px;
}

.services-btn h1{
    font-family: 'Raleway', sans-serif;
    font-weight: 800;
    font-size: 20px;
    margin: 8px 26px;
    color:white
}

.services-btn p{
    line-height: 2px;
    color:#e6a8f4;
    margin: 10px 30px;
    margin-bottom:18px;
    font-size: 14px;
}

.services-btn{
    transition: 0.3s;
}

.services-btn:hover{
    background-color:#b90edf;
    transform: scale(1.05);
    transition: 0.3s;
    box-shadow: 0 8px 8px  #b76bbc;
}

@media(max-width:768px){
    .services-card{
        display:flex;
        flex-direction: row;
        margin: 30px 120px;
        border: none;
        width: 30em;
        
    }
}

@media(max-width:480px){

    .services{
        display:grid;
        grid-template-columns: 1fr;
    }

    .services-card{
        width: 400px;
        margin: 20px 30px;
    }

    .services-body p{
        margin-bottom: 25px;
        font-size: 18px;
    }
}

@media(max-width:420px){

    .services-card{
        display: flex;
        flex-direction: column;
        margin: 20px 30px;
        width: 360px;
        align-items: center;
    }
}

@media(max-width: 360px){

    .services-card{
        display: flex;
        flex-direction: column;
        margin: 20px 30px;
        width: 310px;
        align-items: center;
    }

    .services-body p{
        margin-bottom: 20px;
        font-size: 15px;
    }
}

@media(max-width:320px){
    .services-card{
        display: flex;
        flex-direction: column;
        margin: 20px 30px;
        width: 280px;
        align-items: center;
    }

    .services-body h1{
        font-size: 18px;
    }

    .services-card p{
        font-size:16px;
    }

    .services-btn h1{
        font-family: 'Raleway', sans-serif;
        font-weight: 800;
        margin: 8px 30px;
        color:white;
        font-size: 16px;
    }
    
    .services-btn p{
        line-height: 2px;
        color:#e6a8f4;
        margin: 10px 30px;
        margin-bottom:18px;
        font-size: 12px;
    }
}